.feature-list {
  list-style: none;
  margin: 0;
  padding: 0; }
  .feature-list li {
    list-style: none;
    margin: 0;
    padding: 0; }
  .feature-list .presence-icon {
    float: right;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.3); }
    .feature-list .presence-icon svg {
      vertical-align: text-bottom; }
    .feature-list .presence-icon.hasFeature {
      color: #40d9ac; }
  .feature-list .panel {
    margin-bottom: 10px; }
  .feature-list .panel-title .glyphicon {
    margin-left: 5px;
    font-weight: normal;
    color: rgba(0, 0, 0, 0.3);
    font-size: 0.9em; }
  .feature-list .panel-title .collapsed .glyphicon {
    -webkit-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    transform: rotate(-90deg); }
  .feature-list .panel-body {
    padding: 10px; }



