.app nav.main .collapse {
  display: none;
  margin-left: 1em; }

.app nav.main .logo-placeholder img {
  max-height: 100%;
  max-width: 100%; }

@media (max-width: 992px) {
  .app nav.main .left .top-nav, .app nav.main .right {
    display: none; }
  .app nav.main .left {
    -ms-flex-positive: 0;
    flex-grow: 0; }
  .app nav.main .collapse {
    display: block; } }



